<template>
  <!-- 视频主页面 -->
  <div class="videopage">
    <div class="videopage__router">
      <router-link :class="{videopage__router__item:true, 'videopage__router__item-active':item.name==currentRoute}"
      v-for="item in routerList"
      :key="item.name"
      @click="currentRoute=item.name"
      :to="`/VideoPage/${item.path}`">
        {{ item.name }}
      </router-link>
    </div>
    <div class="videopage__view">
    <router-view></router-view>
    </div>
  </div>
</template>

<script>

export default {
  name: 'VideoPage',
  data () {
    return {
      currentRoute: '视频',
      routerList: [
        {
          name: '视频',
          path: ''
        },
        {
          name: 'MV',
          path: 'MV'
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.videopage{
  height: 100%;
  &__router{
    display: flex;
    align-items: center;
    padding: 20px;
    &__item{
      margin-right: 20px;
      font-size: 14px;
      font-weight: 300;
      color: #111111;
      &-active{
        font-size: 16px;
        font-weight: 600;
        &::after{
          display: block;
          content: '';
          margin: 0 auto;
          width: 90%;
          height: 4px;
          border-radius: 2px;
          background-color: red;
        }
      }
    }
  }
  &__view{
    height: calc(100% - 65px);
    overflow: auto;
  }
}
</style>
